html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Merienda", cursive;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 92%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 2rem;
}

img, picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Merienda", cursive;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.boton-amarillo {
  background-color: #D8DB26;
  font-weight: bold;
  color: #ffffff;
  border-radius: 5px;
  display: inline-block;
  padding: 1.5rem;
  position: center;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
}
.boton-amarillo:hover {
  background-color: #aeb11d;
  cursor: pointer;
}

.boton-gris {
  background-color: #5A625A;
  font-weight: bold;
  color: #ffffff;
  border-radius: 5px;
  display: inline-block;
  padding: 1.5rem;
  position: center;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
}
.boton-gris:hover {
  background-color: #424742;
  cursor: pointer;
}

.boton-verde {
  background-color: #3EA532;
  font-weight: bold;
  color: #ffffff;
  border-radius: 5px;
  display: inline-block;
  padding: 1.5rem;
  position: center;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
}
.boton-verde:hover {
  background-color: #2f7e26;
  cursor: pointer;
}

.boton-rojo {
  background-color: #CD2121;
  font-weight: bold;
  color: #ffffff;
  border-radius: 5px;
  display: inline-block;
  padding: 1.5rem;
  position: center;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
}
.boton-rojo:hover {
  background-color: #a11a1a;
  cursor: pointer;
}

.boton-azul {
  background-color: #377CBD;
  font-weight: bold;
  color: #ffffff;
  border-radius: 5px;
  display: inline-block;
  padding: 1.5rem;
  position: center;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
}
.boton-azul:hover {
  background-color: #2c6295;
  cursor: pointer;
}

.contenedor-nosotros {
  display: grid;
  grid-template-columns: repeat(2, 2fr);
  column-gap: 2rem;
}

.alerta-error {
  background-color: #CD2121;
  text-align: center;
}
.alerta-error p {
  color: #ffffff;
}

.alerta-exito {
  background-color: #3EA532;
  text-align: center;
  padding: 0.1px 0 0.1px 0;
}
.alerta-exito p {
  color: #ffffff;
}

.tabla-resultados {
  border-spacing: 0;
  width: 100%;
  text-align: center;
  font-size: 2rem;
}
.tabla-resultados thead {
  background-color: #377CBD;
}
.tabla-resultados thead th {
  padding: 1rem;
}

.header {
  background-color: #80B6E7;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    justify-content: space-between;
    padding: 3rem 2rem 0 0;
  }
  .contenido-header .navegacion .barra {
    display: inline;
  }
}
.barra {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.barra a {
  padding-right: 2.5rem;
  color: #ffffff;
}
.barra a:last-of-type {
  padding-right: 0;
}

a:hover {
  background-color: #11406A;
}

.logo .logo-pagina {
  margin-bottom: 1rem;
}

.imagen-header {
  padding-bottom: 0;
}
.imagen-header img {
  height: 40rem;
}

.barra-fija {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #5A625A;
}

.footer {
  background-color: #80B6E7;
  padding-top: 1rem;
  margin-top: 1rem;
}

.parrafo {
  text-align: center;
}

@media (min-width: 768px) {
  .navegacion .barra-footer {
    display: flex;
    flex-direction: row;
    column-gap: 2rem;
  }
}
.barra-footer {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
.barra-footer a {
  color: #ffffff;
}

.galeria {
  background-color: #ffffff;
  display: grid;
  gap: 2rem;
}
.galeria .libro {
  background-color: #d3c8c8;
}
.galeria .libro img {
  width: 100%;
  height: 38rem;
  margin-bottom: 0.5rem;
}

@media (min-width: 768px) {
  .galeria {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria {
    grid-template-columns: repeat(3, 1fr);
  }
}
.tabla-libros {
  font-size: 2rem;
}
.tabla-libros tr th {
  font-weight: bold;
  font-size: 2.3rem;
}

.boton-libro {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.contenedor h1 {
  margin-top: 0;
}

.descripcion p {
  text-align: center;
}

.eligenos .anuncios {
  display: grid;
  gap: 2rem;
}
.eligenos .anuncios .anuncio {
  background-color: #d3c8c8;
}

.anuncio .logo {
  text-align: center;
}

.texto p {
  text-align: center;
}

@media (min-width: 480px) {
  .eligenos .anuncios {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 768px) {
  .eligenos .anuncios {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .eligenos .anuncios {
    grid-template-columns: repeat(4, 1fr);
  }
}
.contactar {
  margin-top: 1rem;
  background-image: url(..//img/contactar.jpg);
  text-align: center;
  padding: 1rem;
}
.contactar h4 {
  margin-top: 2rem;
  color: #ffffff;
}

.articulo {
  background-color: #f1e7e7;
}
.articulo .datos-autor p span {
  font-weight: bold;
}

.formulario {
  font-size: 2rem;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
}
.formulario input:not([type=submit]), .formulario textarea {
  border: 1px solid #d3c8c8;
  border-radius: 9px;
  width: 100%;
  margin: 0 0 2rem 0;
  padding: 0.7rem 0 0.7rem 0;
}

.parrafo-contacto span {
  font-weight: bold;
}